import React, { useState, useRef } from 'react';
import { StyleSheet, TouchableOpacity, Dimensions, View, Platform,
// Âä®Áîª
UIManager,
// Âä®Áîª
Animated,
// Âä®Áîª
PanResponder } from 'react-native';
import Icon from '../Icon';
if (Platform.OS === 'android' && UIManager.setLayoutAnimationEnabledExperimental) {
  UIManager.setLayoutAnimationEnabledExperimental(true);
}
const DEVICE_WIDTH = Dimensions.get('window').width;
const DEVICE_HEIGHT = Dimensions.get('window').height;
function DragDrawer(props) {
  const {
    drawerBackgroundColor = '#fff',
    drawerHeight = 300,
    children,
    icon,
    style
  } = props;
  const [animatedViewHeight, setAnimatedViewHeight] = useState(new Animated.Value(drawerHeight));
  const [viewHeight, setViewHeight] = useState(drawerHeight);
  const [showAnimate, setShowAnimate] = useState(false);
  const dynamicDrawerStyles = {
    backgroundColor: drawerBackgroundColor,
    top: null,
    bottom: 0,
    height: drawerHeight,
    width: '100%'
  };

  /**
   * ÊãñÊõ≥ÊïàÊûúÂä®Áîª
   *  */
  const panResponder = useRef(PanResponder.create({
    onMoveShouldSetPanResponder: () => true,
    onPanResponderGrant: () => {},
    onPanResponderMove: (evt, gestureState) => {
      return Animated.event([null, {
        dy: animatedViewHeight
      }], {
        listener: () => {
          let values = DEVICE_HEIGHT - 30 - gestureState.moveY;
          setViewHeight(values);
          animatedViewHeight.setValue(values > 50 ? values : 50);
        },
        useNativeDriver: false
      } // ÂèØÈÄâÁöÑÂºÇÊ≠•ÁõëÂê¨ÂáΩÊï∞
      )(evt, gestureState);
    },
    onPanResponderRelease: (e, gestureState) => {
      let values = DEVICE_HEIGHT + 30 - gestureState.moveY;
      if (gestureState.dy >= 0) {
        //Âêë‰∏ãÊªëÂä®
        if (DEVICE_HEIGHT - e.nativeEvent.pageY < 100) {
          //ÁªìÊùü‰ΩçÁΩÆÂú®ÊäòÂè†Á∫ø‰∏Ä‰∏ã ÂàôÂÖ≥Èó≠
          setViewHeight(30);
          Animated.timing(animatedViewHeight, {
            toValue: 50,
            duration: 300,
            useNativeDriver: false
          }).start();
        } else {
          //ÁªìÊùü‰ΩçÁΩÆÂú®ÊäòÂè†Á∫ø‰∏Ä‰∏ä ÂàôÊäòÂè†
          setShowAnimate(false);
          animatedViewHeight.flattenOffset();
        }
      } else {
        //Âêë‰∏äÊªëÂä®
        if (values > drawerHeight) {
          //ÁªìÊùü‰ΩçÁΩÆÂú®ÊäòÂè†Á∫ø‰∏Ä‰∏ã ÂàôÊäòÂè†
          setShowAnimate(false);
          setViewHeight(drawerHeight);
          Animated.timing(animatedViewHeight, {
            toValue: drawerHeight,
            duration: 300,
            useNativeDriver: false
          }).start();
        } else {
          //ÁªìÊùü‰ΩçÁΩÆÂú®ÊäòÂè†Á∫ø‰∏Ä‰∏ä ÂàôÊâìÂºÄ
          setShowAnimate(true);
          animatedViewHeight.flattenOffset();
        }
      }
    }
  })).current;

  /**
   * Ëá™ÂÆö‰πâÂõæÊ†á
   *  */
  const IconCustom = icon => {
    if (icon) {
      return <>{typeof icon === 'string' ? <Icon name={icon} size={25} color="#8F8F8F" /> : icon}</>;
    } else {
      return <Icon name="minus" size={25} color="#8F8F8F" />;
    }
  };
  return <Animated.View style={StyleSheet.flatten([styles.drawer, dynamicDrawerStyles, style, {
    height: animatedViewHeight
  }])}>
      <Animated.View style={[styles.viewPosition]} {...panResponder.panHandlers}>
        <TouchableOpacity activeOpacity={1}>
          <View style={[styles.homeContainer]}>{IconCustom(icon)}</View>
        </TouchableOpacity>
      </Animated.View>
      {children}
    </Animated.View>;
}
const styles = StyleSheet.create({
  viewPosition: {
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center'
  },
  homeContainer: {
    width: 50,
    height: 50,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center'
  },
  //ÊäΩÂ±âÊ†∑Âºè
  drawer: {
    position: 'absolute',
    top: 0,
    bottom: 0,
    flex: 1,
    zIndex: 3004
  }
});
export default DragDrawer;